/* General Reset */
body, html {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Font */
  .-oneX-body--primary {
    color: #212121;
    font-family: "Segoe UI", Arial, sans-serif;
  }
  
  /* Quote Button */
  .red-oval-button {
    display: inline-block;
    background-color: #d62311;
    color: white;
    text-decoration: none;
    border: none;
    border-radius: 999px;
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: background-color 0.2s ease-in-out;
  }
  
  .red-oval-button:hover {
    background-color: #b81c0d;
  }
  
  /* Header Section */
  .header-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: .5rem auto;
    padding: 1rem;
    max-width: 1000px;
  }
  
  .header-text h1 {
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }
  
  .tagline {
    font-size: 1rem;
    margin-bottom: .5rem;
    color: #333;
  }
  
  /* Action + Social Icons */
  .icon-label {
    margin-top: 0.25rem;
    font-size: 0.9rem;
    transition: all 0.2s ease;
    border-bottom: 2px solid transparent;
  }
  
  a:hover .icon-label {
    border-bottom: 2px solid #d62311;
  }
  
  /* About Section */
  .about-container-wrapper {
    padding: .5rem;
    max-width: 1000px;
    margin: 0 auto;
  }
  
  .about-container {
    display: flex;
    flex-direction: row;
    gap: .25rem;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
  }
  
  .about-text {
    flex: 1 1 300px;
    max-width: 600px;
  }
  
  .about-photo {
    flex: 1 1 300px;
    text-align: center;
  }
  
  .profile-img {
    max-width: 300px;
    width: 100%;
    border-radius: 10px;
  }
  
  /* Social Icons Row */
  .social-icons {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: .25rem;
    flex-wrap: wrap;
  }
  
  .social-link {
    text-decoration: none;
    color: #000;
    text-align: center;
  }
  
  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .header-container {
      flex-direction: column-reverse;
    }
  
    .about-container {
      flex-direction: column;
      align-items: center;
    }
  
    .about-text, .about-photo {
      text-align: center;
    }
  
    .header-text h1 {
      font-size: 1.5rem;
    }
  }
  /* Section Divider */
.section-divider {
    width: 100%;
    height: 1px;
    background-color: #eee;
    margin: .25rem auto;
  }
  
